@import '../../styles/Colors.scss';

.carousel-indicators {
  position: absolute;
  transform: translateX(-50%);
  left: 50%;
  bottom: 15vh;
  z-index: 2;

  .carousel-indicator {
    width: 15px;
    height: 15px;
    border: none;
    background: $mediumBlue;
    opacity: 0.5;
    margin: 0.2em;
    border-radius: 50%;
    cursor: pointer;
  }

  .active {
    opacity: 1;
  }
}